<template>
  <div id="player">
    <div class="player-container row justify-content-center">
      <div id="gameContainer" />
      <div class="ui-container">
        <!-- choice container -->
        <div id="choice" class="card bg-dark text-light rounded" v-if="sceneIndex == 2">
          <div class="card-title">
